/* Two-column layout for NBSTUP Membership Account page */
.pmpro-nbstup-account-layout {
  display: flex;
  gap: 32px;
  align-items: flex-start;
  margin: 20px 0;
}

.pmpro-nbstup-account-sidebar {
  flex: 0 0 280px;
  max-width: 280px;
  padding: 24px;
  position: sticky;
  top: 20px;
  background: #ffffff;
  border: 1px solid #e1e5e9;
  border-radius: 8px;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.1);
}
.pmpro-nbstup-account-sidebar .pmpro-nbstup-account-nav ul {
  list-style: none;
  margin: 0;
  padding: 0;
}
.pmpro-nbstup-account-sidebar .pmpro-nbstup-account-nav li + li {
  margin-top: 4px;
}
.pmpro-nbstup-account-sidebar .pmpro-nbstup-account-nav a {
  display: block;
  padding: 12px 16px;
  border-radius: 6px;
  color: #374151;
  text-decoration: none;
  font-weight: 500;
  transition: all 0.2s ease;
  border: 1px solid transparent;
}
.pmpro-nbstup-account-sidebar .pmpro-nbstup-account-nav a:hover, .pmpro-nbstup-account-sidebar .pmpro-nbstup-account-nav a:focus {
  background-color: #f3f4f6;
  color: #111827;
  border-color: #d1d5db;
}
.pmpro-nbstup-account-sidebar .pmpro-nbstup-account-nav a.active {
  background-color: #3b82f6;
  color: #ffffff;
  border-color: #3b82f6;
}
.pmpro-nbstup-account-sidebar .pmpro-nbstup-account-nav a.active:hover {
  background-color: #2563eb;
  border-color: #2563eb;
}

.pmpro-nbstup-account-content {
  flex: 1 1 auto;
  min-width: 0;
  padding: 24px;
  background: #ffffff;
  border: 1px solid #e1e5e9;
  border-radius: 8px;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.1);
}
.pmpro-nbstup-account-content h2,
.pmpro-nbstup-account-content h3 {
  color: #111827;
  margin-top: 24px;
  margin-bottom: 16px;
}
.pmpro-nbstup-account-content h2:first-child,
.pmpro-nbstup-account-content h3:first-child {
  margin-top: 0;
}
.pmpro-nbstup-account-content h2 {
  font-size: 1.5rem;
  font-weight: 700;
}
.pmpro-nbstup-account-content h3 {
  font-size: 1.125rem;
  font-weight: 600;
}
.pmpro-nbstup-account-content p {
  color: #374151;
  line-height: 1.6;
  margin-bottom: 12px;
}

/* Deceased Members Table Styles */
.pmpro-nbstup-deceased-table {
  width: 100%;
  border-collapse: collapse;
  margin: 20px 0;
  font-size: 0.95rem;
}
.pmpro-nbstup-deceased-table th,
.pmpro-nbstup-deceased-table td {
  padding: 12px 16px;
  text-align: left;
  border-bottom: 1px solid #e5e7eb;
}
.pmpro-nbstup-deceased-table th {
  background-color: #f9fafb;
  font-weight: 600;
  color: #374151;
  border-bottom: 2px solid #d1d5db;
  text-transform: uppercase;
  font-size: 0.85rem;
  letter-spacing: 0.5px;
}
.pmpro-nbstup-deceased-table tbody tr:hover {
  background-color: #f9fafb;
}
.pmpro-nbstup-deceased-table tbody tr:hover .button {
  opacity: 1;
}
.pmpro-nbstup-deceased-table tbody tr:last-child td {
  border-bottom: 1px solid #d1d5db;
}
.pmpro-nbstup-deceased-table .member-avatar {
  width: 48px;
  height: 48px;
  border-radius: 50%;
  object-fit: cover;
}
.pmpro-nbstup-deceased-table .button {
  padding: 8px 16px;
  border-radius: 6px;
  text-decoration: none;
  font-weight: 500;
  display: inline-block;
  transition: all 0.2s ease;
  cursor: pointer;
  border: 1px solid transparent;
  font-size: inherit;
  line-height: inherit;
}
.pmpro-nbstup-deceased-table .button:active, .pmpro-nbstup-deceased-table .button:focus {
  outline: 2px solid rgba(59, 130, 246, 0.5);
  outline-offset: 2px;
}
.pmpro-nbstup-deceased-table .button {
  background-color: #3b82f6;
  color: #ffffff;
  border-color: #3b82f6;
}
.pmpro-nbstup-deceased-table .button:hover {
  background-color: #2563eb;
  border-color: #2563eb;
}
.pmpro-nbstup-deceased-table .button {
  opacity: 0.9;
  transition: all 0.3s ease;
}

/* Pagination Styles */
.pmpro-nbstup-pagination {
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 16px;
  margin-top: 24px;
  flex-wrap: wrap;
  justify-content: center;
}
.pmpro-nbstup-pagination .button {
  padding: 8px 16px;
  border-radius: 6px;
  text-decoration: none;
  font-weight: 500;
  display: inline-block;
  transition: all 0.2s ease;
  cursor: pointer;
  border: 1px solid transparent;
  font-size: inherit;
  line-height: inherit;
}
.pmpro-nbstup-pagination .button:active, .pmpro-nbstup-pagination .button:focus {
  outline: 2px solid rgba(59, 130, 246, 0.5);
  outline-offset: 2px;
}
.pmpro-nbstup-pagination .button {
  background-color: #6b7280;
  color: #ffffff;
  border-color: #6b7280;
}
.pmpro-nbstup-pagination .button:hover {
  background-color: #4b5563;
  border-color: #4b5563;
}
.pmpro-nbstup-pagination span {
  color: #6b7280;
  font-weight: 500;
}
.pmpro-nbstup-pagination .button:disabled,
.pmpro-nbstup-pagination .button[aria-disabled=true] {
  opacity: 0.5;
  cursor: not-allowed;
}
.pmpro-nbstup-pagination .button:disabled:hover,
.pmpro-nbstup-pagination .button[aria-disabled=true]:hover {
  background-color: #6b7280;
  border-color: #6b7280;
}

.pmpro-nbstup-badge {
  display: inline-block;
  padding: 4px 12px;
  border-radius: 6px;
  font-size: 0.85rem;
  font-weight: 600;
  text-transform: uppercase;
  letter-spacing: 0.5px;
}
.pmpro-nbstup-badge.badge-success {
  background-color: rgba(16, 185, 129, 0.1);
  color: #10b981;
}
.pmpro-nbstup-badge.badge-warning {
  background-color: rgba(245, 158, 11, 0.1);
  color: #f59e0b;
}
.pmpro-nbstup-badge.badge-danger {
  background-color: rgba(239, 68, 68, 0.1);
  color: #ef4444;
}
.pmpro-nbstup-badge.badge-info {
  background-color: rgba(59, 130, 246, 0.1);
  color: #3b82f6;
}

/* Tablet Breakpoint (1024px) */
@media (max-width: 1024px) {
  .pmpro-nbstup-account-layout {
    gap: 24px;
  }
  .pmpro-nbstup-account-sidebar {
    flex: 0 0 240px;
    max-width: 240px;
    padding: 20px;
  }
}
/* Mobile Breakpoint (768px) */
@media (max-width: 768px) {
  .pmpro-nbstup-account-layout {
    flex-direction: column;
    gap: 20px;
    margin: 12px 0;
  }
  .pmpro-nbstup-account-sidebar {
    flex: 0 0 auto;
    max-width: 100%;
    position: static;
    padding: 16px;
  }
  .pmpro-nbstup-account-sidebar .pmpro-nbstup-account-nav a {
    padding: 12px 12px;
  }
  .pmpro-nbstup-account-content {
    padding: 16px;
  }
  .pmpro-nbstup-account-content h2 {
    font-size: 1.25rem;
  }
  .pmpro-nbstup-account-content h3 {
    font-size: 1rem;
  }
  .pmpro-nbstup-deceased-table {
    margin: 16px 0;
  }
  .pmpro-nbstup-deceased-table th,
  .pmpro-nbstup-deceased-table td {
    padding: 8px 12px;
    font-size: 0.9rem;
  }
  .pmpro-nbstup-deceased-table th {
    font-size: 0.8rem;
  }
  .pmpro-nbstup-deceased-table .member-avatar {
    width: 40px;
    height: 40px;
  }
  .pmpro-nbstup-deceased-table .button {
    padding: 4px 12px;
    font-size: 0.85rem;
  }
  .pmpro-nbstup-pagination {
    gap: 12px;
    margin-top: 16px;
  }
  .pmpro-nbstup-pagination .button {
    padding: 8px 12px;
    font-size: 0.9rem;
  }
}
/* Small Mobile Breakpoint (480px) */
@media (max-width: 480px) {
  .pmpro-nbstup-account-layout {
    gap: 16px;
    margin: 12px 0;
  }
  .pmpro-nbstup-account-sidebar {
    padding: 12px;
  }
  .pmpro-nbstup-account-content {
    padding: 12px;
  }
  .pmpro-nbstup-account-content h2 {
    font-size: 1.125rem;
    margin-bottom: 12px;
  }
  .pmpro-nbstup-account-content h3 {
    font-size: 0.95rem;
  }
  .pmpro-nbstup-deceased-table th,
  .pmpro-nbstup-deceased-table td {
    padding: 4px;
  }
}
@media print {
  .pmpro-nbstup-account-sidebar,
  .pmpro-nbstup-pagination,
  .button {
    display: none;
  }
  .pmpro-nbstup-account-layout {
    gap: 0;
  }
  .pmpro-nbstup-account-content {
    box-shadow: none;
    border: none;
  }
  .pmpro-nbstup-deceased-table {
    border: 1px solid #000;
  }
  .pmpro-nbstup-deceased-table th,
  .pmpro-nbstup-deceased-table td {
    border: 1px solid #000;
  }
}